.login{
    background-color: black;
    height: 100vh;
    display: grid;
    place-items: center;
}

.login img{
    width: 25%;
}

.login a{
    padding: 20px;
    border-radius: 99px;
    background-color: #1db954;
    font-weight: 700;
    color: whitesmoke;
    text-decoration: none;
}